.page {
  font-family: "Poppins", sans-serif;
  color: #FFFFFF;
  line-height: 140%;
}

body {
  margin: 0;
}


/* #region header */
.header {
  box-sizing: border-box;
  padding: 24px 20px 88px;
  background-image: url(../images/header-bg.png);
  height: 100vh;
  background-size: cover;
  background-position: center right;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.header__top{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width:768px){
  .header{
    background-position: 80% center;
  }
}
.header__icons{
  display: flex;
  gap: 24px;
}

.header__title{
  margin: 0;
  text-align: center;
  font-style: Poppins;
  font-weight: 700;
  font-size: 36px;
  line-height: 100%;
  letter-spacing: -1.5px;
}
 /* #endregion */
/* #region menu */
.menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  box-sizing: border-box;
  padding: 24px 20px 88px;
  height: 100vh;
  background-color: #C2C2C2;
  opacity: 0;
  pointer-events: none;
}
.menu:target{
  opacity: 1;
  pointer-events: all;
}
.menu__top{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu__icons{
  display: flex;
}
/* #endregion */
/* #region icon */
.icon{
  display: block;
  height: 24px;
  width: 24px;
}

.icon--phone{
  background-image: url(../images/icons/phone-call.png);
}

.icon--menu{
  background-image: url(../images/icons/burger-menu.png);
}

.icon--close {
  background-image: url(../images/icons/icon-close.png);
}
/* #endregion */

